
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import StatList from '@/components/attendance/StatList'
import LingLoading from '@/components/attendance/LingLoading'
export default {
name: 'PrincipalAttendanceHome',
components: {
Group,
XHeader,
Cell,
StatList,
Flexbox,
FlexboxItem,
XButton,
LingLoading
},
data() {
return {
selectTime: '',
statList: [
{ label: "", number: 99 },
{ label: "", number: 99 },
{ label: "", number: 99 }
],
statList1: [
{ label: "", number: 1 },
],
statList2: [
{ label: "", number: 3 },
{ label: "", number: 2 }
],
}
},
methods: {
fetchData: function(formData = {}) {

let user = sessionStorage.getItem('CURRENT_USER')
this.$store.dispatch('actionSchoolAttendance', {...{date: this.getSelectTime, schoolId: JSON.parse(user).schoolId}, ...formData})
}
},
computed: {
/**
* 
* @return {Array} 
*/
getTeacherStatList() {
let arr = [], info = this.$store.getters.getSchoolAttendance
arr.push({label: '', number: info.allTeacherNum})
arr.push({label: '', number: info.teacherArriveNum})
arr.push({label: '', number: info.teacherNoArriveNum})
return arr
},
/**
* 
* @return {Array} 
*/
getStudentStatList() {
let arr = [], info = this.$store.getters.getSchoolAttendance
arr.push({label: '', number: info.allStudentNum})
arr.push({label: '', number: info.studentArriveNum})
arr.push({label: '', number: info.studentNoArriveNum})
return arr
},
/**
* 
* @returns {getLingLoading}
*/
getLoadingState: function() {
return this.$store.getters.getLingLoading
},
/**
* 
* @returns {*}
*/
getSelectTime: function() {
return this.$parent.$data.selectTime
}
},
watch: {
/**
* 
* @param val
*/
getSelectTime: function(val) {
this.fetchData({data: val})
}
},
created() {
this.fetchData()
}
}



